html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-block {
  background-color: #006767;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  font-size: 2rem;
}
.boton-block:hover {
  background-color: #004e4e;
  cursor: pointer;
}

.boton-inlineblock {
  background-color: #006767;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  font-size: 2rem;
}
.boton-inlineblock:hover {
  background-color: #004e4e;
  cursor: pointer;
}

.boton-rojo {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  font-size: 2rem;
  width: 100%;
}
.boton-rojo:hover {
  background-color: #a40606;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  font-size: 2rem;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  font-size: 2rem;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-inline-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  font-size: 2rem;
}
.boton-amarillo-inline-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .icon {
  filter: invert(100%);
}
.dark-mode .asesoria li {
  color: #FFFFFF;
}
.dark-mode .texto-noticia h4 {
  color: #000000;
}
.dark-mode .contenedor-noticia img {
  border-radius: 1rem;
}
.dark-mode .imagen-noticia::after {
  content: "";
  display: block;
  width: 80rem;
  height: 0.2rem;
  background-color: #FFFFFF;
  margin-top: 1rem;
  margin: auto;
  margin-top: 2rem;
}
.dark-mode .formulario legend,
.dark-mode .formulario label,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}

.header {
  background-color: #006767;
  padding: 1rem 0 3rem 0;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 15rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
}

.footer {
  background-color: #006767;
  padding: 2.5rem 0;
  margin-bottom: 0;
  margin-top: 5rem;
}
.footer .contenedor-footer {
  padding: 2.5rem;
}
.footer .contenedor-footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  font-size: 2.4rem;
  text-decoration: none;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/** Menú Responsive **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-profesionales {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
    margin: auto;
  }
}
.profesional {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .profesional {
    margin-top: auto;
  }
}
.profesional img {
  border-radius: 50%;
  clip-path: circle(50%);
  width: 300px;
  height: 300px;
  margin: auto;
}
.profesional h4 {
  margin-top: 0;
  text-align: center;
}
.profesional a {
  text-align: center;
}

.imagen-contacto {
  background-image: url(../img/contactar.jpg);
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 700;
  color: #FFFFFF;
}
.imagen-contacto p {
  color: #FFFFFF;
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contenedor-noticias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.noticia {
  background-color: #008181;
  border: 1px solid #404040;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .noticia {
    height: 63rem;
  }
}
@media (min-width: 768px) {
  .noticia {
    margin-top: auto;
  }
}
.noticia .contenido-noticia {
  padding: 2rem;
  text-align: center;
}
.noticia h4 {
  margin: 0;
  color: #FFFFFF;
  font-weight: 400;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #006767;
  margin-top: 1rem;
}
.texto-entrada span {
  color: #E08709;
}

.testimonial {
  background-color: #006767;
  font-size: 1.8rem;
  padding: 0.3rem;
  margin-bottom: 1rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
  margin-right: 1rem;
}

@media (min-width: 768px) {
  .contenedor-productos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
.producto {
  background-color: #008181;
  border: 1px solid #404040;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .producto {
    margin-top: auto;
  }
}
.producto p {
  text-align: center;
  color: #FFFFFF;
}
.producto span {
  font-weight: 700;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .iconos-servicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.iconos-servicios {
  margin-top: 5rem;
}
.iconos-servicios .icono p {
  font-size: 1.8rem;
}

.texto-servicios a {
  display: block;
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-profesional {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 3rem;
  }
}
.texto-profesional h3 {
  font-weight: 700;
  background-color: #006767;
  color: #FFFFFF;
}
.texto-profesional h4 {
  margin-top: 4rem;
  font-weight: 400;
}
.texto-profesional p {
  font-size: 2.2rem;
}

@media (min-width: 768px) {
  .contenedor-asesoria {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.asesoria {
  border-radius: 1rem;
  border: 0.5px solid #008181;
  margin-bottom: 2rem;
}
.asesoria h3 {
  font-size: 2.7rem;
  background-color: #006767;
  border-radius: 0.6rem 0.6rem 0 0;
  height: 10rem;
  text-align: center;
  margin-top: 0;
  color: #FFFFFF;
  margin-bottom: 1rem;
  max-width: 1000rem;
}
.asesoria h4 {
  margin-top: 0;
  margin-bottom: 1rem;
  text-align: center;
}
.asesoria ul {
  list-style: none;
  margin-top: 0;
}

@media (min-width: 768px) {
  .seccion-noticias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-noticia {
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-top: 2rem;
}
.contenedor-noticia img {
  border-radius: 1rem;
}

.texto-noticia {
  padding: 2rem;
  text-align: center;
}
.texto-noticia h4 {
  font-size: 1.8rem;
  font-weight: 400;
}
.texto-noticia a {
  margin-top: 0;
}

.parte-superior h1 {
  text-align: left;
  font-weight: 700;
  line-height: 4rem;
}
.parte-superior h3 {
  text-align: left;
  line-height: 4rem;
  font-size: 2.4rem;
}
.parte-superior p {
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.5rem;
}
.parte-superior p:last-child::after {
  content: "";
  display: block;
  width: 10rem;
  height: 0.2rem;
  background-color: #e1e1e1;
  margin-top: 1rem;
  margin: auto;
  margin-top: 2rem;
}

.imagen-noticia::after {
  content: "";
  display: block;
  width: 80rem;
  height: 0.2rem;
  background-color: #333333;
  margin-top: 1rem;
  margin: auto;
  margin-top: 2rem;
}

.blog-superior h1 {
  text-align: left;
  font-weight: 700;
  line-height: 4rem;
}
.blog-superior h3 {
  text-align: left;
  line-height: 4rem;
  font-size: 2.4rem;
}
.blog-superior p::after {
  content: "";
  display: block;
  width: 50rem;
  height: 0.2rem;
  background-color: #e1e1e1;
  margin-top: 1rem;
  margin: auto;
  margin-top: 2rem;
}
.blog-superior span {
  color: #E08709;
}

.contenedor-admin {
  width: 95%;
  max-width: 160rem;
  margin: 0 auto;
}

.botones-admin {
  text-align: center;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

table.servicios {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
